// tslint:disable-next-line:no-unused-variable
import { render } from 'enzyme';
import * as React from 'react';

import DescriptionComponent from '../../components/checkout-delivery-options.description';

describe('CheckoutDeliveryOptions unit tests - Description', () => {
    it('renders correctly with description', () => {
        const mockProps = {
            description: 'description'
        };
        const component = render(<DescriptionComponent {...mockProps} />);
        expect(component).toMatchSnapshot();
    });

    it('renders correctly with description not provided', () => {
        const mockProps = {
        };
        const component = render(<DescriptionComponent {...mockProps} />);
        expect(component).toMatchSnapshot();
    });
});
